import {  useState } from 'react';
import { BiUser } from 'react-icons/bi';
import { RiLockPasswordLine } from 'react-icons/ri';
import { GoGistSecret } from 'react-icons/go';
import { toast } from 'react-toastify';
import { RegisterApi } from 'utils/adminApi';
import { useNavigate } from 'react-router-dom';

const Register = () => {
  const [regForm, setLoginForm] = useState({
    username: '',
    passwd: '',
    comfirmPasswd: '',
		secretCode: ''
  });

	const navigate = useNavigate()

  const handleClick = (e) => {
    e.preventDefault();
    if (
      regForm.comfirmPasswd !== '' &&
      regForm.username !== '' &&
      regForm.passwd !== ''
    ) {
      if (regForm.comfirmPasswd === regForm.passwd) {
        RegisterApi(regForm).then((res) => {
					localStorage.setItem('__web_token', res.data.token)
					navigate('/admin')
        });
      } else {
        toast.error('两次密码不一致');
      }
    } else {
      toast.error('请输入帐号密码及安全码');
    }
  };

  const handleInput = (e) => {
    setLoginForm({ ...regForm, [e.target.name]: e.target.value });
  };


  return (
    <div className="h-screen flex justify-center bg-slate-500 p-24">
      <div className="w-2/6 h-96 flex flex-col bg-zinc-50 p-8 gap-4">
        <div className="w-full h-8 leading-8 text-center   font-bold text-3xl">
          后台管理系统-注册
        </div>

        <div className="w-full h-8 leading-8 text-center   ">
          请输入您的账号和密码
        </div>
        <div className="flex  w-full h-10  bg-white items-center ">
          <input
            className="flex-auto outline-none ml-2 "
            type="text"
            name="username"
            value={regForm.username}
            onChange={handleInput}
            placeholder="用户名"
          ></input>
          <BiUser className="mr-2" />
        </div>
        <div className="flex bg-white  items-center w-full h-10 ">
          <input
            type="password"
            name="passwd"
            className="flex-auto outline-none ml-2"
            value={regForm.passwd}
            onChange={handleInput}
            placeholder="登陆密码"
          ></input>
          <RiLockPasswordLine className=" mr-2" />
        </div>
        <div className="flex bg-white  items-center w-full h-10 ">
          <input
            type="password"
            name="comfirmPasswd"
            className="flex-auto outline-none ml-2"
            value={regForm.comfirmPasswd}
            onChange={handleInput}
            placeholder="确认密码"
          ></input>
          <RiLockPasswordLine className=" mr-2" />
        </div>

        <div className="w-full h-10 flex ">
          <input
            type="text"
            className="h-full flex-auto outline-none ml-2"
            name="secretCode"
            value={regForm.secretCode}
            onChange={handleInput}
            placeholder="安全码"
          ></input>
          <GoGistSecret className="mr-2" />
        </div>

        <button
          className="w-full h-12 bg-sky-500 mt-4 font-bold text-white"
          onClick={handleClick}
        >
          注册
        </button>
      </div>
    </div>
  );
};

export default Register;
